.techs {
  display: flex;
  flex-wrap: wrap;
  margin: 32px auto;
  align-items: stretch;
  justify-content: center;

  @include xs {
    margin: 32px -10px; }

  &__item {
    display: flex;
    justify-content: center;
    min-width: 85px;
    margin: 0 0 30px;
    flex-direction: column;
    padding: 0 5px;
    width: 110px;

    @include tablet {
      min-width: auto;
      width: 80px; } }

  &__link {
    text-decoration: none;
    color: #fff;
    transition: color .1s;

    &:hover {
      text-decoration: underline;
      color: $themeColor; } }

  &__img-box {
    height: 50px;
    flex: none;
    width: 100%;

    margin: 0 0 9px;
    @include xs {
      margin: 0 0 0; }
    picture {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%; }
    img {
      max-height: 100%;
      max-width: 100%; } }

  &__title {
    font-size: 12px;
    text-align: center;
    margin-top: 10px; } }
