.s-full-ticket {
  padding: 100px 0 10px;

  @include md {
    padding: 80px 0 10px; }

  @include sm {
    padding: 40px 0 10px; }

  .container {
    max-width: 1260px; } }

.full-ticket {
  display: flex;
  margin: 70px 0 50px;
  gap: 20px;
  flex-wrap: wrap;

  @include md {
    margin: 50px -10px 50px;
    flex-wrap: wrap;
    gap: 10px; }

  @include sm {
    margin: 50px -10px 30px;
    flex-wrap: wrap; }

  &__item {
    position: relative;
    display: flex;
    flex: 1 0;
    flex-direction: column;
    font-size: 16px;
    line-height: 1.28;
    padding: 34px 20px;
    min-height: 250px;
    color: #fff;
    background: $mainBg;
    border: 5px solid $gray2;

    &.full-width {
      min-width: 100%;
      flex-direction: row;

      @include sm {
        flex-direction: column; }

      .full-ticket {

        &__left {
          display: flex;
          flex-direction: column;
          width: 418px;
          margin-right: 50px;
          flex: 1 0 auto;
          @include md {
            width: 300px; }

          @include sm {
            width: auto;
            margin-right: 0; } }

        &__title {
          margin: 0; }

        &__img-wrap {
          margin: 24px 0 58px; }

        &__desc {
          margin: 0;
          flex-direction: column;
          text-align: left;
          justify-content: center;
          align-items: flex-start;

          p {
            &:not(:first-child) {
              margin: 8px 0 0; } }

          p {
            &:nth-child(2) {
              margin-top: 30px; } } } } }

    @include md {
      padding: 20px 20px 20px;
      min-height: 250px;
      min-width: calc(50% - 10px); }

    @include sm {
      margin: 0 auto;
      flex: 1 1 calc(50% - 10px);
      min-height: 250px; }

    @include xxs {
      flex: 1 1 100%;
      min-height: 250px; } }

