  //  content: ''
  //  position: absolute
  //  left: 0
  //  top: 0
  //  right: 0
  //  bottom: 0
  //  background: rgba(0, 0, 0, 0.6)
  //  z-index: 0

  //&:hover
  //  .full-ticket
  //    &__bg
  //      opacity: 1

  a.full-ticket__item {
    text-decoration: underline;

    &:hover {
      text-decoration: none; } }

  &__bg {
    position: absolute;
    left: -5px;
    top: -5px;
    right: -5px;
    bottom: -5px;
    background-position: center center;
    background-size: cover;
    opacity: 0;
    filter: brightness(0.7);
    transition: all ease 0.1s; }

  &__title {
    position: relative;
    font-size: clamp(24px, 4vw, 34px);
    text-transform: uppercase;
    font-weight: 900;
    line-height: 0.9;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 0 12px;
    min-height: 92px;
    z-index: 1;
    order: 1; }

  &__img-wrap {
    position: relative;
    width: 100%;
    height: 60px;
    margin: 60px 0 96px;
    z-index: 1;
    order: 0; }

  &__img {
    display: block;
    height: 100%;
    width: auto;
    margin: 0 auto; }

  &__desc {
    position: relative;
    display: flex;
    font-size: 14px;
    line-height: 1.42;
    align-items: center;
    justify-content: center;
    margin: auto 0 0;
    text-align: center;
    min-height: 62px;
    z-index: 1;
    order: 1;

    @include md {
      min-height: 80px; }

    @include sm {
      min-height: 60px; } } }
